.prices-wrapper {
  margin: 50px 40px 20px 40px;
  min-height: calc(100vh - 50px - 20px - 136px);

  @media (max-width: 888px) {
    margin: 50px 10px 20px 10px;
  }
}
.prices__title {
  padding: 10px 10px 10px 80px;
  font-family: "Inter";
  font-size: 20px;
  font-weight: 600;

  @media (max-width: 555px) {
    padding: 40px 10px 10px 10px;
  }
}

.prices-contianer {
  border-top: 1px solid #000;

  display: grid;
  gap: 10px;
  grid-template-columns: 420px 1fr;

  @media (max-width: 1050px) {
    grid-template-columns: 20% 1fr;
  }

  @media (max-width: 1150px) {
    grid-template-columns: 320px 1fr;
  }
  @media (max-width: 960px) {
    display: block;
  }
}

.prices-contianer__column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @media (min-width: 1500px) {
    flex-direction: row;
  }
  @media (max-width: 888px) {
    margin-bottom: 30px;
  }
}

.prices-cart__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 20px 0 0 40px;
  min-height: 150px;

  @media (max-width: 1500px) {
  }
  @media (max-width: 620px) {
    display: block;
  }
}

.prices-comtianer__column {
  @media (max-width: 1200px) {
  }
}

.column__title {
  padding: 20px;
  font-family: "Inter";
  font-size: 18px;
  font-weight: 500;
}

.column__list {
  padding: 5px 0 0 20px;
}

.column__list-item {
  font-family: "Inter";
  font-size: 16px;
  font-weight: 300;
  // text-align: right;

  @media (max-width: 620px) {
    text-align: right;
  }
}

.column-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  padding: 5px 40px 0 0;

  @media (min-width: 1500px) {
    display: block;
  }

  @media (max-width: 620px) {
    margin-bottom: 15px;
    padding: 5px 0 0 0;
  }

  // @media (max-width: 550px) {
  //   display: grid;
  //   grid-template-columns: 30% 1fr;
  //   padding-bottom: 20px;
  // }
}

.column-item__price {
  font-family: "Montserrat";
  font-size: 14px;
  font-weight: 300;
}

.column-item__title {
  position: relative;
  font-family: "Inter";
  font-size: 16px;
  font-weight: normal;
  padding-bottom: 20px;

  @media (max-width: 620px) {
    padding-bottom: 5px;
  }
}

.column-item__count {
  position: absolute;
  top: -2px;
  left: -30px;
  font-family: "Montserrat";
  font-size: 14px;
  font-weight: 700;

  // @media (max-width: 550px) {
  //   top: auto;
  //   bottom: -17px;
  //   left: 5px;
  // }
}

.column-item__list {
  font-family: "Inter";
  font-size: 16px;
  font-weight: 300;

  @media (max-width: 555px) {
    text-align: end;
  }
}

.column-item__list-item {
  @media (max-width: 620px) {
    text-align: right;
  }
  @media (max-width: 360px) {
    font-size: 13px;
  }
}

//footer
.footer-wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
