.portfolio-grid__item {
  position: relative;
  overflow: hidden;

  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;

  //   max-width: 300px;
  max-height: 350px;

  object-fit: cover;

  &:hover img {
    filter: grayscale(0) brightness(1);
    transition: all 1s ease;
  }

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;

    filter: grayscale(0) brightness(0.6);
    transition: all 1s ease;
  }
}

.portfolio-grid__title {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translate(-50%);
  background: rgba(0, 0, 0, 0.95);
  color: #fff;
  padding: 5px 10px;

  white-space: nowrap;

  z-index: 1000;

  font-size: 14px;

  span {
    font-size: 12px;
  }
}
