.contacts-container {
  margin: 50px 40px 20px 40px;

  @media (max-width: 800px) {
    margin: 70px 10px 20px 10px;
  }
  @media (max-width: 520px) {
    margin: 80px 10px 20px 10px;
  }
}

.contacts__subtitle {
  padding: 0 10px 10px 80px;
  font-family: "Inter";
  font-size: 14px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.5);
}

.contacts__title {
  padding: 10px 10px 0 80px;
  font-family: "Inter";
  font-size: 20px;
  font-weight: 600;
}

.contacts__item {
  display: grid;
  grid-template-columns: 1fr 1fr;
  border-top: 1px solid #000;
}

.contacts__item:last-child {
  @media (max-width: 888px) {
    grid-template-columns: 30% 1fr;
  }
  @media (max-width: 490px) {
    display: block;
  }
}

.contacts__item-title {
  padding-top: 10px;
  font-family: "Inter";
  font-size: 18px;
  font-weight: 500;
}

.contacts__item-list {
  // @media (max-width: 888px) {
  //   text-align: end;
  // }
}

.contacts__item-list-title {
  padding-top: 5px;

  font-family: "Inter";
  font-size: 14px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.5);
  text-align: center;
}

.contacts__item-list-content {
  font-family: "Inter";
  font-size: 16px;
  font-weight: 300;

  padding-bottom: 5px;
}

.contacts__social {
  display: flex;
  justify-content: end;

  @media (max-width: 888px) {
    flex-wrap: wrap;
  }
  @media (max-width: 490px) {
    flex-wrap: wrap;
    justify-content: center;
  }
}

.contacts__social-card {
  position: relative;

  width: 120px;
  height: 120px;

  margin: 10px;

  border-radius: 8px;

  border: 1px solid #000;

  transition: all 0.5s ease;

  &:hover {
    transform: scale(1.05);
    transition: all 0.5s ease;

    background-color: rgba(255, 255, 255, 0.1);
  }
}

.card__title {
  padding: 5px;

  font-family: "Inter";
  font-size: 16px;
  font-weight: 300;

  color: #000;
}

.card__icon {
  position: absolute;
  right: 5px;
  bottom: 5px;

  color: #000;
}

.prices-contianer__about {
  border-top: 1px solid #000;

  display: grid;
  gap: 10px;
  grid-template-columns: 420px 1fr;

  @media (max-width: 1050px) {
    grid-template-columns: 20% 1fr;
  }

  @media (max-width: 1150px) {
    grid-template-columns: 320px 1fr;
  }
  @media (max-width: 960px) {
    display: block;
  }
}

.prices-contianer__column-about {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @media (max-width: 888px) {
    margin-bottom: 30px;
  }
}

.wrapper-ft {
  display: flex;
  flex-direction: column;
  align-content: space-between;
  min-height: calc(100vh - 136px);
}

.about-container {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;

  margin-top: 50px;

  color: #fff;

  @media (max-width: 780px) {
    display: block;
  }
}

.about-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: #8d634cac;
}

.about-container__item {
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.about-container__item:first-child {
  @media (max-width: 780px) {
    height: 300px;
  }
}

.about-container__item:last-child {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 40px;
}

.about__subtitle {
  font-family: "Inter";
  font-size: 14px;
  font-weight: 200;

  z-index: 101;
}

.about__title {
  font-family: "Inter";
  font-size: 20px;
  font-weight: 600;

  z-index: 101;
}

.about__content {
  padding-top: 20px;
  text-align: justify;

  z-index: 101;
}

.about-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  padding: 5px 20px 0 0;

  @media (max-width: 620px) {
    margin-bottom: 15px;
    padding: 5px 0 0 0;
  }
}
