@use "./reset";
@use "./vars";
@use "./utils";
@use "./sticky-footer";

body {
  color: var(--text-color);
  font-family: var(--font-main);
  overflow-x: hidden;
  background: linear-gradient(
    135deg,
    rgb(238, 238, 238) 50%,
    rgb(193, 193, 193) 100%
  );
  background-attachment: fixed;

  width: 100%;
}

.main {
  width: 100%;
}
