.my-masonry-grid {
  display: -webkit-box; /* Not needed if autoprefixing */
  display: -ms-flexbox; /* Not needed if autoprefixing */
  display: flex;

  margin: 20px 0 20px 0; /* gutter size offset */
  width: auto;
}
.my-masonry-grid_column {
  padding-left: 10px; /* gutter size */
  background-clip: padding-box;
}

/* Style your items */
.my-masonry-grid_column > div {
  /* change div to reference your elements you put in <Masonry> */
  background: grey;
  margin-bottom: 10px;
}

.case-wrapper {
  position: relative;
  margin: 60px 20px 0 20px;
}

.case-wrapper__one {
  position: relative;
  margin: 70px 20px 0 20px;
}

.wrapper-button {
  position: absolute;
  top: 0px;
  left: 0;
  right: 20px;
  display: flex;
  justify-content: end;
  align-items: center;

  gap: 10px;
}

.wrapper-button__one {
  position: absolute;
  top: -25px;
  left: 0;
  right: 20px;
  display: flex;
  justify-content: end;
  align-items: center;

  gap: 10px;
}

.wrapper-button__repost {
  position: static !important;
}

.menu-item {
  justify-content: center;
  align-items: center;
  display: flex;
  padding: 8px;
  width: 100%;
}

.wrapper-button-item {
  z-index: 1;
}

// .ant-image-mask {
