// }

.ant-tabs-nav {
  margin-left: 20px !important;
  margin-right: 20px !important;
}

.ant-tabs-nav-list {
  margin-left: 20px !important;
}

.wrapper-widget {
  display: flex;
  justify-content: end;
  align-items: center;
  position: sticky;
  gap: 20px;

  bottom: 0;

  z-index: 1000;
  padding-right: 30px;
}

.ant-float-btn-group {
  position: relative !important;
}
